.collection-item {
    display: flex;
    width: 30%;
    height: 350px;
    flex-direction: column;

    .image {
        width: 100%;
        height: 95%;
        background-size: cover;
        background-position: center;
        margin-bottom: 5px;
    }

    .collection-footer {
        width: 100%;
        height: 5%;
        display: flex;
        justify-content: space-between;
        font-size: 18px;

        .name {
            width: 90%;
            margin-bottom: 5px;
        }

        .price {
            width: 10%;
        }
    }
}